.contactForm {
  margin-top: 15px;
  margin-bottom: 15px;
  padding: 10px;
  width: 500px;
  border: 2px solid #33c4ac;
  display: flex;
  flex-direction: column;
}
.contactLabel {
  margin-bottom: 15px;
  display: flex;
  gap: 5px;
  flex-direction: column;
  font-size: 18px;
  font-weight: 500;
}

.contactInput {
  font-size: 15px;
  padding: 5px;
  font-style: italic;
  border-color: #33c4ac;
}
.contactInput:focus {
  outline: none;
}

.contactSubmitBtn {
  font-size: 18px;
  padding: 5px;
  width: fit-content;
  border: 2px solid transparent;
  background-color: #33c4ac;
  border-radius: 5px;
  box-shadow: 0px 4px 10px rgba(139, 139, 139, 0.5);
  cursor: pointer;
}

.contactSubmitBtn:focus,
.contactSubmitBtn:hover {
  font-weight: 500;
  color: #33c4ac;
  border: 2px solid #33c4ac;
  background-color: transparent;
}
