.contactList {
  padding: 15px 10px;
}
.contactItem {
  width: 500px;
  margin-bottom: 10px;
  font-size: 18px;
  display: flex;
  align-items: center;
}
.contactNumber {
  margin-left: 10px;
}

.contactDeleteBtn {
  margin-left: auto;
  font-size: 12px;
  padding: 2px;
  width: fit-content;
  border: 1px solid black;
  background-color: #33c4ac;
  border-radius: 4px;
  box-shadow: 0px 4px 10px rgba(139, 139, 139, 0.5);
  cursor: pointer;
  text-align: center;
}
